@import "https://fonts.googleapis.com/css2?family=Inter:opsz,wght@14..32,100..900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
}

body {
  min-height: 100vh;
  background: linear-gradient(#eee, #c8c7ff);
  justify-content: center;
  align-items: center;
  display: flex;
}

.chatbot-popup {
  width: 90vw;
  max-width: 480px;
  height: 90vh;
  max-height: 600px;
  background: #fff;
  border-radius: 15px;
  flex-direction: column;
  display: flex;
  position: relative;
  box-shadow: 0 0 128px rgba(0, 0, 0, .1), 0 32px 64px -48px rgba(0, 0, 0, .5);
}

.chat-header {
  background: #5350c4;
  justify-content: space-between;
  align-items: center;
  padding: 15px 22px;
  display: flex;
}

.chat-header .header-info {
  align-items: center;
  gap: 10px;
  display: flex;
}

.header-info .chatbot-logo {
  height: 35px;
  width: 35px;
  fill: #5350c4;
  background: #fff;
  border-radius: 50%;
  flex-shrink: 0;
  padding: 6px;
}

.header-info .logo-text {
  color: #fff;
  font-size: 1.31rem;
  font-weight: 600;
}

.chat-header #close-chatbot {
  color: #fff;
  height: 40px;
  width: 40px;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 50%;
  margin-right: -10px;
  padding-top: 2px;
  font-size: 1.9rem;
  transition: all .2s;
}

.chat-header #close-chatbot:hover {
  background: #5350c4;
}

.chat-body {
  scrollbar-width: thin;
  scrollbar-color: #ccccf5, transparent;
  flex-direction: column;
  flex: 1;
  gap: 15px;
  padding: 20px;
  display: flex;
  overflow-y: auto;
}

.chat-body::-webkit-scrollbar {
  width: 8px;
}

.chat-body::-webkit-scrollbar-thumb {
  background: #ccccf5;
  border-radius: 4px;
}

.chat-body::-webkit-scrollbar-track {
  background: none;
}

.chat-body .message {
  align-items: center;
  gap: 11px;
  display: flex;
}

.chat-body .bot-message .bot-avatar {
  height: 35px;
  width: 35px;
  fill: #fff;
  background: #5350c4;
  border-radius: 50%;
  flex-shrink: 0;
  align-self: flex-end;
  margin-bottom: 2px;
  padding: 6px;
}

.chat-body .user-message {
  flex-direction: column;
  align-items: flex-end;
}

.chat-body .message .message-text {
  max-width: 75%;
  background: #f2f3ff;
  padding: 12px 16px;
  font-size: .95rem;
}

.chat-body .bot-message .message-text {
  background: #f2f3ff;
  border-radius: 13px 13px 13px 3px;
}

.chat-body .user-message .message-text {
  color: #fff;
  background: #5350c4;
  border-radius: 13px 13px 3px;
}

.chat-footer {
  width: 100%;
  background: #fff;
  border-top: 1px solid #cccce5;
  padding: 15px 22px 20px;
}

.chat-footer .chat-form {
  width: 100%;
  background: #fff;
  border-radius: 32px;
  outline: 1px solid #cccce5;
  align-items: center;
  display: flex;
  position: relative;
}

.chat-footer .chat-form:focus-within {
  outline: 2px solid #5350c4;
}

.chat-form .message-input {
  height: 47px;
  width: 100%;
  resize: none;
  border-radius: inherit;
  text-align: left;
  border: none;
  outline: none;
  padding-left: 10px;
  font-size: .95rem;
  line-height: 47px;
  overflow: hidden;
}

.chat-form .chat-controls {
  height: 35px;
  justify-content: center;
  align-items: center;
  gap: 3px;
  padding-right: 6px;
  display: flex;
}

.chat-form .chat-controls button {
  height: 35px;
  width: 35px;
  cursor: pointer;
  color: #706db0;
  background: none;
  border: none;
  border-radius: 50%;
  font-size: 1.15rem;
  transition: all .2s;
}

.chat-form .chat-controls button:hover {
  background: #f1f1ff;
}

em-emoji-picker {
  width: 100%;
  max-width: 350px;
  max-height: 330px;
  visibility: hidden;
  position: absolute;
  top: -337px;
  left: 50%;
  transform: translateX(-50%);
}

body.show-emoji-picker em-emoji-picker {
  visibility: visible;
}

@media (max-width: 768px) {
  .chatbot-popup {
    width: 100vw;
    height: 100vh;
    max-width: none;
    max-height: none;
    border-radius: 0;
  }

  .chat-body {
    gap: 10px;
    padding: 10px 8px;
  }

  .chat-header {
    padding: 10px 12px;
  }

  .header-info .logo-text {
    font-size: 1rem;
  }

  .chat-footer {
    padding: 10px 8px;
  }

  .chat-form .message-input {
    height: 35px;
    font-size: .85rem;
    line-height: 35px;
  }

  .chat-form .chat-controls button {
    height: 25px;
    width: 25px;
    font-size: 1rem;
  }
}

